.nav {
    background-color: #333;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 80px;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
  }
  
  .nav ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
    display: flex;
  }
  
  .nav ul li {
    margin: 0 15px;
  }
  
  .nav ul li a {
    color: #fff;
    font-size: 18px;
    text-decoration: none;
  }
  
  .nav ul li a:hover {
    text-decoration: underline;
    cursor: pointer;
  }
  